import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'

import Home from '@/pages/Home'
import Profile from '@/pages/Profile'
import ProfileSummary from '@/pages/Profile/children/Summary'
import ProfileUpdate from '@/pages/Profile/children/Update'
import Intern from '@/pages/Intern'
import News from '@/pages/News'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main,
      redirect: '/home',
      children: [
        {
          path: '/home',
          name: 'Home',
          component: Home
        },
        {
          path: '/profile',
          // name: 'Profile',
          component: Profile,
          children: [
            {
              path: '',
              name: 'Profile',
              component: ProfileSummary
            },
            {
              path: 'update',
              name: 'ProfileUpdate',
              component: ProfileUpdate
            }
          ]
        },
        {
          path: '/intern',
          name: 'Intern',
          component: Intern
        },
        {
          path: '/news',
          name: 'News',
          component: News
        }
      ]
    }

  ]
})



