import services from '@/services'
import * as types from '../mutation-types'

const state = {
  'id': -1,
  'name': '',
  'sid': '',
  'grade': '',
  'tel': '',
  'email': '',
  'qq': -1,
  'wechat': ''
}

const getters = {
  profile (state) {
    return {
      name: state.name,
      sid: state.sid,
      grade: state.grade,
      tel: state.tel,
      email: state.email,
      qq: state.qq,
      wechat: state.wechat
    }
  }
}

const mutations = {
  [types.PROFILE_DATA_RECEIVED] (state, data) {
    console.log(data)
    Object.assign(state, data)
  }
}

const actions = {
  getProfile ({ dispatch, commit, state, rootState }) {
    services.profile.get(rootState.id).then(res => {
      commit(types.PROFILE_DATA_RECEIVED, res.data)
    }).catch(err => { console.log(err) })
  },
  updateProfile ({ dispatch, commit, state, rootState }, data) {
    return new Promise((resolve, reject) => {
      services.profile.put(rootState.id, data).then(res => {
        console.log('个人信息修改成功')
        // 更新base信息
        // console.log(rootState.base.profile_complete)
        if (rootState.base.profile_complete === false) {
          dispatch('updateBaseData', {profile_complete: true}).then(() => {
            commit(types.PROFILE_DATA_RECEIVED, res.data)
            resolve()
          }).catch((err) => { reject(err) })
        } else {
          commit(types.PROFILE_DATA_RECEIVED, res.data)
          resolve()
        }
      // 初始profile
      // dispatch('getProfile')
      }).catch(err => {
        console.log(err)
      })
    })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



