<template>
  <div class="root">
    <router-view></router-view>
    <!-- loading -->
    <div v-show="isloading" class="loading-layer">
      <div class="loading-spinner">
        <div class="loading-box">
          <div class="loading-text">加载中...</div>
          <img src="./svg/loading-bars.svg" alt="Loading icon" />
        </div>
      </div>
    </div>
    <div class="toast"
      :class="{
        show: toast
      }">
      <div class="toast-mask"></div>
      <div class="actionsheet" @click="handlePrompt">
        <div class="actionsheet-cell toast-title" v-text="intent?intent.msg:''"></div>
        <div class="actionsheet-cell" data-value="ok">确认提交</div>
        <div class="actionsheet-cell" data-value="cancel">取消</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
// import * as apis from
export default {
  name: 'App',
  computed: {
    // isloading () {
    //   return this.$store.state.loading
    // }
    ...mapState({
      isloading: 'loading',
      intent: 'intent'
    }),
    toast () {
      // return this.$store.state.prompt ? {} : {display: 'none'}
      return this.$store.state.prompt
    }
  },
  methods: {
    handlePrompt (event) {
      if (event.target.dataset.value !== undefined) {
        if (event.target.dataset.value === 'ok') {
          this.intent.ok()
        } else if (event.target.dataset.value === 'cancel') {
          this.intent.cancel()
        }
        console.log('close prompt')
        this.$store.dispatch('closePrompt')
      }
    }
  }
  // created () {
  //   console.log('获取数据')
  //   this.$http.get('/api/profile').then((response) => {
  //     console.log(response.data)
  //   }).catch(err => {
  //     console.log(err)
  //   })
  // }
}
</script>

<style lang="stylus">
@import "./assets/stylus/normalize.styl"
.root
  height 100%
.loading-layer
  position fixed
  z-index 100
  top 0
  bottom 0
  left 0
  right 0
  .loading-spinner
    width 100%
    margin-top: 180px;
    .loading-box
      width 120px
      height 120px
      border-radius 20px
      margin 0 auto
      text-align center
      background #000a
      color #fff
      overflow auto
      font-size 14px
      .loading-text
        margin 30px 0 10px
.toast
  // display none
  // position fixed
  // left 0
  // right 0
  // top 0
  // bottom 0
  // background #0008
  // z-index 1000
  &.show
    // display block
    .toast-mask
     display block
    .actionsheet
      transform: translate(0, 0);
  .toast-mask
    display none
    position fixed
    left 0
    right 0
    top 0
    bottom 0
    background #0008
    z-index 1000
  .actionsheet
    position fixed
    left 0
    bottom 0
    z-index: 5000;
    width: 100%;
    background-color: #fff;
    transform: translate(0, 100%);
    transition transform ease-in-out .38s
    .actionsheet-cell
      &::before
        content: " ";
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        height: 1px;
        border-top: 1px solid #D9D9D9;
        color: #D9D9D9;
        transform-origin: 0 0;
        transform: scaleY(0.5);
      &:active
        background-color: #ECECEC;
      position relative
      padding: 10px 0;
      text-align: center;
      font-size: 1.125em;

</style>



