import services from '@/services'
import * as types from '../mutation-types'

const state = {
  // 实习申请书
  application: {},
  // 资格审查
  audit: {}
}

const getters = {
  // profile (state) {
  //   return {
  //     name: state.name,
  //     sid: state.sid,
  //     grade: state.grade,
  //     tel: state.tel,
  //     email: state.email,
  //     qq: state.qq,
  //     wechat: state.wechat
  //   }
  // }
}

const mutations = {
  [types.APPLICATION_DATA_RECEIVED] (state, data) {
    console.log(data)

    state.application = {...Object.assign(state.application, data)}
    // Object.assign(state, {application: data})
    // state.application = Object.assign(state.application, data)
    // state.application = data
  }
}

const actions = {
  // 获取实习申请书信息
  getApplicationData ({ dispatch, commit, state, rootState }) {
    services.intern.application.get(rootState.id).then(res => {
      console.log(res.data)
      commit(types.APPLICATION_DATA_RECEIVED, res.data)
    }).catch(err => { console.log(err) })
  },
  getAuditData ({ dispatch, commit, state, rootState }) {

  },
  applyForInternship ({ dispatch, commit, state, rootState }, data) {
    return new Promise((resolve, reject) => {
      // 先更新服务端和vuex中base数据
      if (rootState.base.intern_state < 1) {
        dispatch('updateBaseData', {intern_state: 1}).then(() => {
          console.log('服务端和vuex中base数据更新成功')
          // 请求更新服务端application数据
          services.intern.application.put(rootState.id, data).then(res => {
            console.log('实习申请书信息修改成功')
            // 更新vuex中application数据
            commit(types.APPLICATION_DATA_RECEIVED, res.data)
            // 通知完成了整个promise
            resolve()
          }).catch((err) => { reject(err) })
        }).catch((err) => { reject(err) })
      } else {
        services.intern.application.put(rootState.id, data).then(res => {
          console.log('实习申请书信息修改成功')
          // 更新vuex中application数据
          commit(types.APPLICATION_DATA_RECEIVED, res.data)
          // 通知完成了整个promise
          resolve()
        }).catch((err) => { reject(err) })
      }
    })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



