<template>
  <section class="form-section">
    <header class="form-section-title">{{title}}</header>
    <main class="form-section-bd">
      <!-- <div class="form-item"></div> -->
      <slot></slot>
    </main>
  </section>
</template>

<script>
export default {
  name: 'form-section',
  props: {
    title: {}
  },
  data () {
    return {}
  }
}
</script>

<style scoped lang="stylus">
.form-section
  background #fff
  border-width 0
  // padding 10px
  .form-section-title
    padding 20px 15px 10px
    font-size 1.125em
    line-height 1
</style>



