<template>
  <div class="main"
    :class="{
      shownav: navisShowed
    }">
    <header class="header"
      :class="{
        athome: isHome
      }">
      <nav class="sidebar">
        <header><div class="user-image"></div></header>
        <div class="nav" @click="navisShowed=false">
          <router-link :to="{name:'Home'}" tag="li">
            <div></div>
            <div class="label">首页</div>
          </router-link>
          <router-link :to="{name:'Profile'}" tag="li">
            <div></div>
            <div class="label">个人信息</div>
          </router-link>
          <router-link :to="{name:'Intern'}" tag="li">
            <div></div>
            <div class="label">实习</div>
          </router-link>
          <router-link :to="{name:'News'}" tag="li">
            <div></div>
            <div class="label">招聘信息</div>
          </router-link>
        </div>
      </nav>
      <div class="drawer-mask" @click="navisShowed=false"></div>
      <div class="user"
       @click="navisShowed=true">
        <span class="user-photo"><img :src="userphoto" alt=""></span>
        <span class="user-name">{{username}}</span>
      </div>
    </header>
    <div class="content">
      <div class="blank"></div>
      <router-view/>
    </div>
    <div class="actionsheet">
      <div class="mask" style=""></div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { resolveName } from '@/store/utils'
const namedResolver = resolveName('base')
export default {
  name: 'Main',
  data () {
    return {
      navisShowed: false
      // isHome: true

      // uid: '',
      // name: '严俊杰',
      // avatar_url: '',
      // type: 'student',
      // profile_complete: false,
      // intern_state: 0,
      // messages_count: 2
    }
  },
  computed: {
    ...mapState({
      username: namedResolver('name'),
      userphoto: namedResolver('avatar_url'),
      isHome: 'home'
    })
    // username: function () {
    //   console.log('computed')
    //   return this.$store.getters.name + '!'
    // },
    // userphoto () {
    //   return ''
    // }
  },
  methods: {

  },
  mounted () {
    console.log('请求基础数据')
    this.$store.dispatch('getBaseData')
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus">
.main
  height 100%
.header
  &.athome
    background transparent
    box-shadow none
    transition: margin ease-in-out .38s;
    .user
      // color #fff
      border-top-right-radius 5px
      border-bottom-right-radius 5px
      background #374046
      box-shadow 0 1px 2px rgba(0,0,0,0.4)
  position fixed
  background #374046
  z-index 50
  width 100%
  height: 50px;
  line-height 50px
  box-shadow 0 1px 2px rgba(0,0,0,0.2)
  .user
    height 34px
    display inline-block
    vertical-align middle
    padding 0 1em
    line-height 34px
    color #000
    .user-photo
      display inline-block
      width 30px
      height 30px
      background-color #eee
      border-radius 16px
      vertical-align top
      margin-top 2px
    .user-name
      margin-left 5px
      color #fff
      // vertical-align middle
  // 侧边栏
  .sidebar
    background #fff
    position fixed
    z-index 100
    top 0
    // bottom 0
    left -200px
    width 200px
    height 100%
    line-height initial
    transition: left ease-in-out .38s;
    header
      height 200px
      background #374046
      .user-image
        width 150px
        height 150px
        border-radius 75px
        background #fefefe
        margin 0 auto
    .nav
      li
        &:active
          background #eee
        height 40px
        .label
          height 100%
          line-height 40px
          text-align center
          width 100px
          margin 0 auto
  .drawer-mask
    position: fixed;
    z-index: 99;
    width: 100%;
    height: 100%;
    top: 0;
    bottom 0
    left: 0;
    right 0
    background: transparent;
    display none
.content
  width 100%
  height 100%
  transition: margin ease-in-out .38s;
  .blank
    height 50px

.shownav .sidebar
  // transform: translateX(50px);
  left 0
  box-shadow 4px 0 8px #0001

.shownav .content
  margin-left 200px
  // transform: translate3d(200px,0,0);

.shownav .header.athome{
  margin-left 150px
  // transition: transform .3s;
  // transform: translateX(150px);
}

.shownav
  .header
    .drawer-mask
      display block
</style>



