<template>
  <section class="module">
    <!-- <div class="module-area"> -->
      <header class="module-hd" v-show="hasTitle">
        <span class="title">{{title}}</span>
      </header>
      <main class="module-bd">
        <slot>没有内容</slot>
      </main>
      <footer class="module-ft">
        <icon class="svg-more" name="more-square" scale="3"></icon>
      </footer>
    <!-- </div> -->
  </section>
</template>

<script>
export default {
  props: {
    hasTitle: {
      type: Boolean,
      default: true
    },
    title: String
  },
  data () {
    return {}
  }
}
</script>

<style scoped lang="stylus">
.module
  padding 10px 15px
  // color #fff
  .module-hd
    height 30px
    line-height 30px
    font-size 1.125em
    // background #aff
    // .title
  .module-bd
    min-height 32px
    // line-height 1
  .module-ft
    width 100%
    height 30px
    line-height 30px
    // background #faf
    text-align right
    .svg-more
      vertical-align middle
      width 30px
      height 30px
        // color #fff
      // &::before
      //   content ""
      // background url('../../../assets/svg/code.svg')
      // background-size: contain;

</style>



