<template>
  <router-link :to="{name:link}" tag="div"  class="message-module"
    :style="styles[0]">
    <Module
      title="我的消息">你有{{messagesCount}}条消息。</Module>
  </router-link>
</template>

<script>
import { mapState } from 'vuex'
import { resolveName } from '@/store/utils'
import Module from './Module'
const baseResolver = resolveName('base')
export default {
  components: {
    Module
  },
  data () {
    return {
      link: '',
      styles: [
        {
          // background: 'linear-gradient(90deg, #f2ca5c 0,#ffab40 100%)'
          background: 'linear-gradient(90deg, #ffca28 0,#ffb74d 100%)'
        }
      ]
    }
  },
  computed: {
    ...mapState({
      messagesCount: baseResolver('messages_count')
    })
  }
}
</script>

<style scoped lang="stylus">
.message-module
  overflow auto
  color #fff
</style>



