<template>
  <div class="list">
    <div v-for="(v,k) in items" :key="k"
        class="list-item">
        <label class="list-item-label">{{k}}</label>
        <List v-if="Object.prototype.toString.call(v)==='[object Object]'"
          :items='v' class="list-item-value"></List>
        <div v-else class="list-item-value">{{v}}</div>
      </div>
  </div>
</template>

<script>
export default {
  name: 'List',
  props: {
    items: {}
  },
  data () {
    return {}
  }
}
</script>

<style scoped lang="stylus">
.list-item
  &:last-child
    border none
  border-bottom 1px solid #ddd
  line-height 1.48
  padding 10px 15px
  font-size 1rem
  label
    display block
    float left
    width 100px
  .list
    clear both
    margin-left 15px

</style>



