<template>
  <div class="audit-status">
    <header>
      <div class="header"><div class="title">实习申请审核进度</div></div>
      <AuditSchedule></AuditSchedule>
    </header>
    <main class="application-info">
      <div class="title">实习申请表内容</div>
      <List
        :items="applicationInfo"></List>
    </main>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import AuditSchedule from './children/AuditSchedule'
import List from '@/components/List'
export default {
  components: {
    AuditSchedule,
    List
  },
  data () {
    return {}
  },
  computed: {
    ...mapState({
      applicationInfo: state => state.intern.application
    })
  },
  mounted () {
    // console.log(this.applicationInfo)
    this.$store.dispatch('getApplicationData')
  }
}
</script>

<style scoped lang="stylus">
.audit-status
  .header
    // &::after
    //   content ''
    //   width 10px
    //   height 10px
    //   position absolute
    //   left 25px
    //   bottom -5px
    //   background #fff
    //   // box-shadow 0 0px 4px rgba(0,0,0,0.1)
    //   transform rotate(45deg)
    position relative
    background #fff
    padding 2px 0
    box-shadow 0 0px 4px rgba(0,0,0,0.1)
  .title
    &::before
      content ''
      width 10px
      height 12px
      display inline-block
      vertical-align middle
      margin-right 5px
      background #ddd
    font-size 1.25em
    line-height 1.2
    // color #fff
    // background #374046
    margin 10px
    margin-left 0
  .application-info
    overflow auto
    background #fff
</style>



