<template>
  <router-link :to="{name:link}" tag="div" class="intern-module"
    :style="styles[isProfileComplete?1:0]">
    <Module
      :title="title">
      <template v-if="internState===0">
        <div v-if="isProfileComplete">
          可以申请实习。
        </div>
        <div v-else>
          无法申请实习，请先完成个人信息。
        </div>
      </template>
    </Module>
  </router-link>
</template>

<script>
import Module from './Module'
export default {
  components: {
    Module
  },
  props: {
    isProfileComplete: {},
    internState: {}
  },
  data () {
    return {
      mapTitle: ['实习申请', '实习申请进度', '实习中'],
      styles: [
        {
          background: 'linear-gradient(90deg, #ff9e80 0,#ff6e40 100%)'
        },
        {
          background: 'linear-gradient(90deg, #80d8ff 0,#40c4ff 100%)'
        }
      ]
    }
  },
  computed: {
    title () {
      return this.mapTitle[this.internState]
    },
    link () {
      return this.isProfileComplete ? 'Intern' : ''
    }
  }
}
</script>

<style scoped lang="stylus">
.intern-module
  overflow auto
  color #fff
</style>



