<template>
  <div class="stage"
    :class="[{
      'stage-detail': showDetail
    },status]">
  <div class="stage-hd" @click="type?stagehdClicked():null">
    <div class="stage-node">
      <div v-if="type===1" class="sign-positive"></div>
      <div v-else-if="type===-1" class="sign-negtive"></div>
      <div v-else class="sign-unknown"></div>
    </div>
    <div class="stage-title">{{title}}</div>
  </div>
  <div class="stage-bd" :style="{
      height: detailHeight
    }">
    <main ref="detailContent">{{content}}</main>
  </div>
</div>

</template>

<script>
export default {
  props: {
    title: {},
    content: {},
    type: {}
  },
  data () {
    return {
      showDetail: false
    }
  },
  computed: {
    detailHeight () {
      // console.log('xxx')
      // console.log(this.showDetail ? this.$refs.detailContent.clientHeight : 0)
      return this.showDetail ? this.$refs.detailContent.clientHeight + 'px' : 0
    },
    status () {
      switch (this.type) {
        case 1: return 'stage-positive'
        case -1: return 'stage-negtive'
        default: return ''
      }
    }
  },
  methods: {
    stagehdClicked () {
      this.showDetail = !this.showDetail
      // console.log(this.$refs.detailContent.clientHeight)
    }
  }
}
</script>

<style scoped lang="stylus">
.stage
  position relative
  padding-bottom 30px
  &::after
    content ""
    position absolute
    top 0
    bottom 0
    left 15px
    // height 100%
    width 10px
    background #cfc
  &.stage-negtive
    .stage-hd
      color #f11
      .stage-node
        background #faa
  &.stage-positive
    &::after
      background #afa
    .stage-hd
      .stage-node
        background #afa
  .stage-hd
    .stage-node
      position relative
      z-index 20
      display inline-block
      vertical-align top
      width 40px
      height 40px
      border-radius 20px
      background #fff
      box-shadow 0 1px 4px rgba(0,0,0,0.12)
      .sign-positive
        width 8px
        height 16px
        border-right 4px solid #fff
        border-bottom 4px solid #fff
        margin 9px auto
        transform rotate(45deg)
      .sign-unknown
        width 20px
        height 20px
        border-radius 10px
        background #afa
        margin 10px auto
      .sign-negtive
        &::before
          content ""
          position absolute
          top 7px
          left -7px
          width 20px
          height 6px
          background #fff
          border-radius 4px
        width 6px
        height 20px
        background #fff
        margin 10px auto
        position relative
        border-radius 4px
        transform rotate(45deg)
    .stage-title
      display inline-block
      vertical-align top
      line-height 40px
  .stage-bd
    // position relative
    color #555
    font-size 0.875rem
    // border-top 1px solid #eee
    margin-left 50px
    overflow hidden
    // max-height: 500px
    box-shadow 0 8px 8px -10px rgba(0,0,0,0.12)
    transition height 0.6s
    main
      padding 10px
      border-radius 10px
      background #fff

.stage:first-child::after
  top 20px
.stage:last-child
  padding-bottom 0
  &::after
    display none
</style>



