<template>
  <router-link :to="{name:link}" tag="div" class="profile-module"
    :style="styles[isProfileComplete?1:0]">
    <Module
      :has-title="isProfileComplete"
      title="个人信息">
      <div v-if="isProfileComplete">
        当前你的个人信息完整。
      </div>
      <div v-else>
        <div>当前你的个人信息不全</div>
        <small>注意：要进行实习申请，需要先完善个人信息</small>
      </div>
    </Module>
  </router-link>
</template>

<script>
import Module from './Module'
export default {
  components: {
    Module
  },
  props: {
    isProfileComplete: {}
  },
  data () {
    return {
      styles: [
        {
          background: 'linear-gradient(90deg, #ff8a80 0,#ff5252 100%)'
        },
        {
          background: 'linear-gradient(90deg, #69f0ae 0,#00e676 100%)'
        }
      ]
    }
  },
  computed: {
    link () {
      return this.isProfileComplete ? 'Profile' : 'ProfileUpdate'
    }
  }
}
</script>

<style scoped lang="stylus">
.profile-module
  overflow auto
  color #fff
</style>



