<template>
  <div class="profile-summery">
    <div v-if="isProfileComplete" class="summery">
      <div class="summery-content">
        <div v-for="(v,k) in profile" :key="k"
          class="profile-item">
          <label>{{k}}</label>
          <div>{{v}}</div>
        </div>
      </div>
      <div class="footer">
        <router-link
          :to="{name:'ProfileUpdate'}" tag="div" class="btn">
          修改个人信息
        </router-link>
      </div>
    </div>
    <div v-else class="notice-box">
      <div class="content">
        <div class="info">当前你的个人信息不全</div>
        <small>注意：要进行实习申请，需要先完善个人信息</small>
      </div>
      <div class="footer">
        <router-link
          :to="{name:'ProfileUpdate'}" tag="div" class="btn btn_plain">
          完善个人信息
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'
import { resolveName } from '@/store/utils'
const baseResolver = resolveName('base')
export default {
  data () {
    return {
      // isProfileComplete: false,
      // profile: {
      //   name: '严俊杰',
      //   sid: 'mf1732159',
      //   grade: 2.1,
      //   tel: 18840820000,
      //   email: 'mf1732159@smail.nju.edu.cn',
      //   qq: 971592476,
      //   wechat: 'yanjunjie'
      // }
    }
  },
  computed: {
    ...mapState({
      isProfileComplete: baseResolver('profile_complete')
    }),
    ...mapGetters({
      profile: 'profile'
    })
  },
  methods: {
    ...mapActions([
      'getProfile'
    ])
  },
  mounted () {
    if (this.isProfileComplete) {
      console.log('请求个人信息数据')
      this.getProfile()
    }
  }
}
</script>

<style scoped lang="stylus">
.profile-summery
  padding 10px 0 0
  height 100%
  .summery
    min-height 60%
    background-color #fff
    padding 16px
    .profile-item
      min-height 40px
      line-height 40px
      position relative
      &::after
        content ""
        display block
        position absolute
        bottom 0
        width 100%
        background #eee
        height 1px
      label
        position absolute
        top 0
        margin 0
      div
        margin-left 90px
        word-break break-all
    .footer
      text-align center
      padding 20px 0

</style>



