// import Vue from 'vue'

import services from '@/services'
import * as types from '../mutation-types'

const state = {
  uid: '',
  name: '',
  avatar_url: '',
  type: '',
  profile_complete: false,
  intern_state: 0,
  messages_count: 0
}

// const getters = {
//   name: state => state.name
// }

const mutations = {
  [types.BASE_DATA_RECEIVED] (state, baseData) {
    state.uid = baseData.uid
    state.name = baseData.name
    state.avatar_url = baseData.avatar_url
    state.type = baseData.type
    state.profile_complete = baseData.profile_complete
    state.intern_state = baseData.intern_state
    state.messages_count = baseData.messages_count
  }
  // [types.PROFILE_ADDED] () {
  //   state.profile_complete =
  // }
}

const actions = {
  getBaseData (context) {
    services.base.get(context.rootState.id).then(res => {
      console.log(res.data)
      context.commit(types.BASE_DATA_RECEIVED, res.data)
    }).catch(err => { console.log(err) })
  },
  updateBaseData ({commit, state, rootState}, data) {
    services.base.put(rootState.id, Object.assign(Object.create(null), state, data)).then(res => {
      console.log('更新了信息', res.data)
      commit(types.BASE_DATA_RECEIVED, res.data)
    }).catch(err => { console.log(err) })
  }
}

export default {
  state,
  // getters,
  mutations,
  actions
}



