import Vue from 'vue'
import * as apis from './apis'

export default {
  application: {
    get: (id) => Vue.axios.get(apis.GET_INTERN + '/application/' + id),
    put: (id, data) => Vue.axios.put(apis.GET_INTERN + '/application/' + id, data)
  },
  audit: {
    get: (id) => Vue.axios.get(apis.GET_INTERN + '/audit/' + id),
    put: (id, data) => Vue.axios.put(apis.GET_INTERN + '/audit/' + id, data)
  }

}



