import * as types from './mutation-types'
export default {
  [types.ONLOAD] (state) {
    state.loading = true
  },
  [types.LOADED] (state) {
    state.loading = false
  },

  [types.OPEN_PROMPT] (state, intent) {
    console.log(intent)
    state.intent = intent
    state.prompt = true
  },
  [types.CLOSE_PROMPT] (state) {
    state.prompt = false
  },
  [types.ENTER_HOME] (state) {
    state.home = true
  },
  [types.LEAVE_HOME] (state) {
    state.home = false
  }
}



