<template>
  <div class="home">
    <ProfileModule class="home-item"
      :isProfileComplete="profileComplete">
    </ProfileModule>
    <InternModule class="home-item"
      :isProfileComplete="profileComplete"
      :internState="internState">
    </InternModule>
    <MessageModule class="home-item"
      >
    </MessageModule>
    <Module class="home-item"
      title="招聘信息"></Module>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import ProfileModule from './children/ProfileModule'
import InternModule from './children/InternModule'
import MessageModule from './children/MessageModule'
import Module from './children/Module'
import { resolveName } from '@/store/utils'
const namedResolver = resolveName('base')
export default {
  components: {
    Module,
    ProfileModule,
    InternModule,
    MessageModule
  },
  data () {
    return {}
  },
  computed: {
    ...mapState({
      profileComplete: namedResolver('profile_complete'),
      internState: namedResolver('intern_state')
    })
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
    // 通过 `vm` 访问组件实例
      vm.$store.dispatch('enterHome')
      console.log(vm)
    })
  },
  beforeRouteUpdate (to, from, next) {
    console.log('xxxxx')
  },
  beforeRouteLeave (to, from, next) {
    this.$store.dispatch('leaveHome')
    next()
  }
}
</script>

<style scoped lang="stylus">
.home
  padding 10px 16px 0
  .home-item
    // height 150px
    margin 0 0 15px
    background #fff
    border-radius 10px
    box-shadow: -1px 12px 30px -6px rgba(0,0,0,0.06)
</style>



