<template>
  <div class="audit-schedule">
    <Stage v-for="(v,index) in schedule" :key="'stage-'+(index+1)"
      :title="v.title"
      :content="v.opinion"
      :type="v.decide"></Stage>
  </div>
</template>

<script>
import Stage from './chidren/Stage'
export default {
  components: {
    Stage
  },
  data () {
    return {
      // detailHeight: 0
      schedule: [
        {
          title: '修学情况确认',
          decide: 1,
          opinion: '已经修得学校及学院规定的40个学分(A类课8学分，B类课8学分，C类课3学分，D类课13学分，毕业论文8学分)。还缺2学分（包括A类课程0学分，B类课程0学分，C类课程0学分, D类课程2学分）。'
        },
        {
          title: '导师意见',
          decide: 1,
          opinion: '小伙子不错，同意实习'
        },
        {
          title: '其他教师意见',
          decide: -1,
          opinion: '我擦，他还在给我干活呢！不准去实习。'
        },
        {
          title: '辅导员意见',
          decide: 0,
          opinion: ''
        },
        {
          title: '工程发展中心意见',
          decide: 0,
          opinion: ''
        }
      ]
    }
  }
}
</script>

<style scoped lang="stylus">
.audit-schedule
  // background #fff
  padding 10px 10px 20px
</style>



