<template>
  <div class="intern-application">
    <header class="intern-application-header">实习申请表</header>
    <form class="intern-application-form">
      <FormSection
        title="实习单位">
        <!-- <div class="form-item">
          <label class="label">公司名称</label>
          <input type="text">
        </div> -->
        <div class="cells">
          <div class="cell">
            <div class="cell-hd">
              <label class="label">公司名称</label>
            </div>
            <div class="cell-bd">
              <input class="input" type="text" placeholder="请输入公司全称"
                v-model="current.company.name">
            </div>
          </div>
          <div class="cell">
            <div class="cell-hd">
              <label class="label">公司地址</label>
            </div>
            <div class="cell-bd">
              <input class="input" type="text" placeholder="请输入公司地址"
                v-model="current.company.address">
            </div>
          </div>
          <div class="cell">
            <div class="cell-hd">
              <label class="label">公司联系人</label>
            </div>
            <div class="cell-bd cell-bd-below">
              <div class="cells">
                <div class="cell">
                  <div class="cell-hd">
                    <label class="label">姓名</label>
                  </div>
                  <div class="cell-bd">
                    <input class="input" type="text" placeholder="请输入联系人姓名"
                      v-model="current.company.attention.name">
                  </div>
                </div>
                <div class="cell">
                  <div class="cell-hd">
                    <label class="label">职务</label>
                  </div>
                  <div class="cell-bd">
                    <input class="input" type="text" placeholder="请输入联系人职务"
                      v-model="current.company.attention.title">
                  </div>
                </div>
                <div class="cell">
                  <div class="cell-hd">
                    <label class="label">电话</label>
                  </div>
                  <div class="cell-bd">
                    <input class="input" type="tel" placeholder="请输入联系人电话"
                      v-model="current.company.attention.tel">
                  </div>
                </div>
                <div class="cell">
                  <div class="cell-hd">
                    <label class="label">邮箱</label>
                  </div>
                  <div class="cell-bd">
                    <input class="input" type="email" placeholder="请输入联系人邮箱"
                      v-model="current.company.attention.email">
                  </div>
                </div>
              </div>
            </div>
          </div>
            <div class="cell">
              <div class="cell-hd">
                <label class="label">工资</label>
              </div>
              <div class="cell-ft">元</div>
              <div class="cell-bd">
                <input class="input" type="number" placeholder="请输入工资"
                  v-model="current.company.salary">
              </div>
            </div>
        </div>
      </FormSection>
      <FormSection
        title="实习时间">
        <div class="cells">
          <div class="cell">
            <!-- <div class="cell-hd">
              <label class="label">工资</label>
            </div> -->
            <div class="cell-bd">
              <input class="input" type="date" placeholder=""
                v-model="current.time.start">
              ~
              <input class="input" type="date" placeholder=""
                v-model="current.time.end">
            </div>
          </div>
        </div>
      </FormSection>
      <FormSection
        title="通过何种方式获得实习">
        <div class="cells">
          <div class="cell">
            <!-- <div class="cell-hd">
              <label class="label">工资</label>
            </div> -->
            <div class="cell-bd">
              <input class="input" type="text" placeholder=""
                v-model="current.access">
            </div>
          </div>
        </div>
      </FormSection>
      <FormSection
        title="申请理由">
        <div class="cells">
          <div class="cell">
            <!-- <div class="cell-hd">
              <label class="label">工资</label>
            </div> -->
            <div class="cell-bd">
              <textarea class="textarea" cols="30" rows="3"
                v-model="current.reason"></textarea>
            </div>
          </div>
        </div>
      </FormSection>
      <FormSection
        title="修学情况确认">
        <div class="cells">
          <div class="cell">
            <!-- <div class="cell-hd">
              <label class="label">工资</label>
            </div> -->
            <div class="cell-bd">
              <textarea class="textarea" cols="30" rows="3"
                v-model="current.credit"></textarea>
            </div>
          </div>
        </div>
      </FormSection>
      <FormSection
        title="是否正在协助老师工作">
        <div class="cells">
          <div class="cell">
            <!-- <div class="cell-hd">
              <label class="label">工资</label>
            </div> -->
            <div class="cell-bd">
              <textarea class="textarea" cols="30" rows="3"
                v-model="current.teachers"></textarea>
            </div>
          </div>
        </div>
      </FormSection>
      <div class="form-ft">
        <div class="btn" @click="submit">提交实习申请</div>
      </div>
    </form>
  </div>
</template>

<script>
import FormSection from '@/components/FormSection'
export default {
  components: {
    FormSection
  },
  data () {
    return {
      current: {
        company: {
          name: '网易',
          address: '成都养猪场',
          attention: {
            name: '丁磊',
            title: 'CEO',
            tel: '110',
            email: 'dinglei@qq.com'
          },
          salary: 15000
        },
        time: {
          start: '2018-07-01',
          end: '2018-10-31'
        },
        access: '校招',
        reason: '已完成课业，父母同意出校学习',
        credit: '不缺少学分',
        teachers: [
          {
            name: '邵栋',
            rel: '助教'
          }
        ]
      }
    }
  },
  methods: {
    submit () {
      console.log('点击了实习申请提交按钮')
      var validate = (data) => {
        return true
      }
      if (validate(this.current)) {
        this.$store.dispatch('intent', {msg: '是否提交实习申请？'}).then(() => {
          console.log('用户确认要提交实习申请')
          this.$store.dispatch('applyForInternship', this.current).then(() => {
            console.log('实习申请成功')
          }).catch(err => { console.log(err) })
        }).catch(err => { console.log(err) })
      }
    }
  }
}
</script>

<style scoped lang="stylus">
.intern-application
  padding-bottom: 40px;

  .intern-application-header
    text-align: center;
    font-size: 1.2em;
    line-height: 1.2;
    font-weight: bold;
    padding: 15px 0 10px;
    height 125px
    color #fff
    background #374046
    margin-bottom -100px

  .intern-application-form
    padding: 0 10px;

    .form-section
      border-radius: 15px;
      padding-bottom 15px
      box-shadow: 0 1px 4px #0002;
      margin-bottom 15px
      input,textarea
        background #fafafa

</style>



