<template>
  <div class="profile-update">
    <form class="profile-from">
      <div class="cells">
        <div class="cell">
          <div class="cell-hd">
            <label class="label">姓名</label>
          </div>
          <div class="cell-bd">
            <input class="input" type="text" placeholder="请输入姓名"
              v-model="current.name">
          </div>
        </div>
        <div class="cell">
          <div class="cell-hd">
            <label class="label">学号</label>
          </div>
          <div class="cell-bd">
            <input class="input" type="text" placeholder="请输入学号"
              v-model="current.sid">
          </div>
        </div>
        <div class="cell">
          <div class="cell-hd">
            <label class="label">年级</label>
          </div>
          <div class="cell-bd">
            <input class="input" type="text" placeholder="请输入年级"
              v-model="current.grade">
          </div>
        </div>
        <div class="cell">
          <div class="cell-hd">
            <label class="label">手机号</label>
          </div>
          <div class="cell-bd">
            <input class="input" type="tel" placeholder="请输入手机号"
              v-model="current.tel">
          </div>
        </div>
        <div class="cell">
          <div class="cell-hd">
            <label class="label">邮箱</label>
          </div>
          <div class="cell-bd">
            <input class="input" type="email" placeholder="请输入邮箱"
              v-model="current.email">
          </div>
        </div>
        <div class="cell">
          <div class="cell-hd">
            <label class="label">QQ</label>
          </div>
          <div class="cell-bd">
            <input class="input" type="number" placeholder="请输入QQ"
              v-model="current.qq">
          </div>
        </div>
        <div class="cell">
          <div class="cell-hd">
            <label class="label">微信</label>
          </div>
          <div class="cell-bd">
            <input class="input" type="text" placeholder="请输入微信"
              v-model="current.wechat">
          </div>
        </div>
      </div>
      <div class="btn-area"><a class="btn" @click="submit">提交修改</a></div>
    </form>
  </div>
</template>

<script>
export default {
  data () {
    return {
      current: {}
    }
  },
  methods: {
    submit () {
      var validate = (data) => {
        if (JSON.stringify(this.current) === '{}') {
          return false
        }
        for (var k in data) {
          if (data.hasOwnProperty(k) && (data[k] === undefined || data[k] == null)) {
            return false
          }
        }
        return true
      }
      if (validate(this.current)) {
        console.log(JSON.stringify(this.current))
        this.$store.dispatch('intent', {msg: '是否提交个人信息？'}).then(() => {
          console.log('用户确认')
          this.$store.dispatch('updateProfile', this.current).then(() => {
            console.log('准备跳转')
            // this.$router.push('/profile' + '/' + this.$store.state.id)
            this.$router.push('/profile')
          }).catch((err) => {
            console.log('提交撤销', err)
          })
        }).catch((err) => {
          console.log('提交撤销', err)
        })
      }
    }
  }
}
</script>

<style scoped lang="stylus">
.profile-update
  background #fff
  height 100%
  padding 10px 0 30px
</style>



