import * as types from './mutation-types'

export default {
  showLoader (context) {
    context.commit(types.ONLOAD)
  },
  hiddenLoader (context) {
    context.commit(types.LOADED)
  },

  showPrompt (context) {
    context.commit(types.OPEN_PROMPT)
  },
  closePrompt (context) {
    context.commit(types.CLOSE_PROMPT)
  },
  enterHome (context) {
    context.commit(types.ENTER_HOME)
  },
  leaveHome (context) {
    context.commit(types.LEAVE_HOME)
  },
  intent ({ dispatch, commit }, {msg}) {
    console.log('处理intent')
    return new Promise((resolve, reject) => {
      commit(types.OPEN_PROMPT, {
        msg,
        ok () {
          resolve()
        },
        cancel () {
          reject(new Error('user cancel'))
        }
      })
      // dispatch('showPrompt')
    })
  }
}



