<template>
  <div class="page-intern">
    <Application v-if="profileComplete && internState === 0"></Application>
    <AuditStatus v-else-if="profileComplete && internState === 1"></AuditStatus>
    <InternRrace v-else-if="profileComplete && internState === 2"></InternRrace>
    <div class="page-intern-notice" v-else>
      <div class="notice-box">
        <div class="content">
          <div class="info">当前你的个人信息不全</div>
          <small>注意：要进行实习申请，需要先完善个人信息</small>
        </div>
        <div class="footer">
          <router-link
            :to="{name:'ProfileUpdate'}" tag="div" class="btn btn_plain">
            完善个人信息
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { resolveName } from '@/store/utils'
import Application from './children/Application'
import AuditStatus from './children/AuditStatus'
import InternRrace from './children/InternRrace'
const baseResolver = resolveName('base')
export default {
  components: {
    Application,
    AuditStatus,
    InternRrace
  },
  data () {
    return {}
  },
  computed: {
    ...mapState({
      profileComplete: baseResolver('profile_complete'),
      internState: baseResolver('intern_state')
    })
  },
  mounted () {
    // console.log('---------\n', this.profileComplete, this.internState)
    // if (this.profileComplete && this.internState === 1) {
    //   console.log('---------')
    // }
  }
}
</script>

<style scoped lang="stylus">
.page-intern
  .page-intern-notice
    padding-top 20px
</style>



